.App {
  text-align: center;
  position: relative;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 250px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.AppMain {
    position: absolute;
  background: url("./miko.jpg") 50% 50% no-repeat;
  background-size: cover;
  height: 300px;
  width: 300px;
  overflow-x: hidden;
  top: -300px;
  -webkit-animation: background-move infinite 20s linear;
          animation: background-move infinite 20s linear;
  border-radius: 1000px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes background-move {
    0% {
        top: -300px;
        left: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }
    10% {
        top: 300px;
        left: 100px;
    }
    50% {
        top: 500px;
        left: 500px;
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
    90% {
        top: 300px;
        left: 100px;
    }
    100% {
        top: -300px;
        left: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }
}

@keyframes background-move {
    0% {
        top: -300px;
        left: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }
    10% {
        top: 300px;
        left: 100px;
    }
    50% {
        top: 500px;
        left: 500px;
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
    90% {
        top: 300px;
        left: 100px;
    }
    100% {
        top: -300px;
        left: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }
}



